/* add styles here */
@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir-Bold;
  src: url(fonts/Avenir-Heavy.ttf);
}

[data-qa="big"],
[data-qa="small"] {
  margin-top: 20px;
}

.search__image {
  position: absolute;
  background-image: url("images/Search.svg");
  background-repeat: no-repeat;
  color: #3D4E61;

}

.image--big {
  background-size: 19px;
  height: 19px;
  width: 19px;
  left: 34px;
  top: 46px;
}

.image--small {
  background-size: 11px;
  height: 11px;
  width: 11px;
  left: 21px;
  top: 125px;
}

.search__input {
  padding: 0;

  width: calc(100vw - 18px);

  border-radius: 4px;
  border: 1px solid #E1E7ED;
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.10);

  color: #3D4E61;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.input--big {
  height: 68px;
  font-size: 16px;
  text-indent: 62px;
}

.input--small {
  height: 40px;
  font-size: 14px;
  text-indent: 33px;
}

.input--big:hover {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.search__input:focus {
  border-radius: 3px;
  background: linear-gradient(180deg, #FFF 0%, #F6F6F7 100%);
  outline: none;
}

.input--big:focus {
  text-indent: 62px;
}

.input--small:focus {
  text-indent: 33px;
}

.input--small:hover {
  box-shadow: 0 3px 8px 0 rgba(61, 78, 97, 0.20);
}

.input--big:active {
  text-indent: 62px;
}
